import React from 'react'
import { Switch, Route } from 'react-router-dom'

import Home from './pages/Home'
import Admin from './pages/Admin'
import Question from './pages/Question'
import Pratice from './pages/Pratice'
import SearchPratice from './pages/SearchPratice'
import PraticeResult from './pages/PraticeResult'

import PDF from './pages/PDF'

export default function Routes() {
   return (
      <Switch>
         <Route path="/" exact component={Home} />
         <Route path="/admin" component={Admin} />
         <Route path="/questao/:id" component={Question} />
         <Route path="/simulados/:id/resultado" component={PraticeResult} />
         <Route path="/simulados/:id" component={Pratice} />
         <Route path="/simulados" component={SearchPratice} />

         <Route path="/pdf/:id" component={PDF} />
      </Switch>
   )
}
