import React, { Component } from 'react'
import {
   FaSearch,
   FaFilter,
   FaTrashAlt,
   FaHighlighter,
   FaPencilAlt,
   FaPencilRuler,
   FaRegComment,
} from 'react-icons/fa'
import Select from 'react-select'
import { toast } from 'react-toastify'

import api from '../../services/api'

import {
   Container,
   Title,
   FormContainer,
   TextHeader,
   Form,
   ButtonsContainer,
   SubmitButton,
   ClearButton,
   QuestionsContainer,
   QuestionsList,
   QuestionHeader,
   QuestionStatement,
   AlternativesList,
   AnswerContainer,
   AnswerContent,
   AnswerCorrect,
   AnswerIncorrect,
   OptionsContainer,
} from './styles'
import { Link } from 'react-router-dom'

export default class Home extends Component {
   state = {
      exam_selected: {},
      exams: [
         { value: 'poscomp', label: 'POSCOMP' },
         { value: 'enade', label: 'ENADE' },
      ],
      areas_selected: {},
      exam_areas: [],
      poscomp_areas: [
         { value: '1', label: 'Matemática' },
         { value: '2', label: 'Fundamentos de Computação' },
         { value: '3', label: 'Tecnologia da Computação' },
      ],
      enade_areas: [],
      questions: [],
   }

   async componentDidMount() {
      const response = await api.get('/questions')

      this.setState({ questions: response.data })
   }

   handleChangeExam = event => {
      const { exam_selected, poscomp_areas, enade_areas } = this.state

      if (exam_selected !== event) {
         this.setState({
            exam_selected: event,
            areas_selected: [],
            exam_areas: event.value === 'poscomp' ? poscomp_areas : enade_areas,
         })
      }
   }

   handleAddArea = option => {
      this.setState({
         areas_selected: option,
      })
   }

   handleSubmitButton = async event => {
      event.preventDefault()

      const { exam_selected, areas_selected } = this.state
      if (Object.keys(exam_selected).length === 0) {
         toast.error('Selecione um concurso')
         return
      }

      const url =
         Object.keys(areas_selected).length === 0
            ? ''
            : `&area=${areas_selected.value}`

      const response = await api.get(
         `/questions?exam=${exam_selected.value}` + url
      )

      this.setState({ questions: response.data })
   }

   handleClearForm = event => {
      event.preventDefault()

      this.setState({
         exam_selected: {},
         areas_selected: [],
      })
   }

   handleVerifyAnwser = (question, user_answer) => {
      const { questions } = this.state

      questions[question].user_answer = user_answer

      this.setState({ questions })
   }

   render() {
      const {
         exam_selected,
         exams,
         areas_selected,
         exam_areas,
         questions,
      } = this.state

      return (
         <Container>
            <Title>
               <FaSearch size={24} />
               <span>Buscar Exercícios</span>
            </Title>
            <FormContainer>
               <TextHeader>
                  <FaFilter size={18} />
                  <span>Filtros</span>
               </TextHeader>
               <Form>
                  <p>Concurso</p>
                  <Select
                     isSearchable={false}
                     isClearable
                     value={exam_selected}
                     options={exams}
                     onChange={this.handleChangeExam}
                  />
                  <p>Área</p>
                  <Select
                     isSearchable={false}
                     isClearable
                     value={areas_selected}
                     options={exam_areas}
                     onChange={this.handleAddArea}
                  />
                  <ButtonsContainer>
                     <SubmitButton onClick={this.handleSubmitButton}>
                        <FaSearch size={16} />
                        <span>Buscar</span>
                     </SubmitButton>
                     <ClearButton onClick={this.handleClearForm}>
                        <FaTrashAlt size={16} />
                        <span>Limpar</span>
                     </ClearButton>
                  </ButtonsContainer>
               </Form>
            </FormContainer>
            <QuestionsContainer>
               <TextHeader>
                  <FaHighlighter size={18} />
                  <span>Questões</span>
               </TextHeader>
               <QuestionsList>
                  {questions.map((question, index) => (
                     <li key={question.id}>
                        <QuestionHeader>
                           <strong>
                              <span>1</span>
                              (POSCOMP/{question.year} - Questão{' '}
                              {question.number})
                           </strong>
                           <span>#{question.id}</span>
                        </QuestionHeader>
                        <QuestionStatement
                           dangerouslySetInnerHTML={{
                              __html: question.statement,
                           }}
                        />
                        <AlternativesList>
                           {question.alternatives.map(alternative => (
                              <li key={alternative.letter}>
                                 <strong>{alternative.letter})</strong>
                                 <span
                                    dangerouslySetInnerHTML={{
                                       __html: alternative.content,
                                    }}
                                 />
                              </li>
                           ))}
                        </AlternativesList>
                        {typeof question.user_answer === 'undefined' ? (
                           <AnswerContainer>
                              <p>Escolha sua resposta</p>
                              <AnswerContent>
                                 {question.alternatives.map(alternative => (
                                    <button
                                       key={alternative.letter}
                                       onClick={() =>
                                          this.handleVerifyAnwser(
                                             index,
                                             alternative.letter
                                          )
                                       }
                                    >
                                       {alternative.letter}
                                    </button>
                                 ))}
                              </AnswerContent>
                           </AnswerContainer>
                        ) : question.user_answer === question.answer ? (
                           <AnswerCorrect>
                              <FaPencilAlt />
                              <span>
                                 Você acertou essa questão! Sua Resposta:{' '}
                                 {question.user_answer}
                              </span>
                           </AnswerCorrect>
                        ) : (
                           <AnswerIncorrect>
                              <FaPencilRuler />
                              <span>
                                 Você errou! Resposta Correta: {question.answer}
                                 . Sua Resposta: {question.user_answer}
                              </span>
                           </AnswerIncorrect>
                        )}
                        <OptionsContainer>
                           <Link to={``}>
                              <FaSearch />
                              <span>Questões Similares</span>
                           </Link>
                           <Link to={`/questao/${question.id}`}>
                              <FaRegComment />
                              <span>Detalhes da Questão</span>
                           </Link>
                        </OptionsContainer>
                     </li>
                  ))}
               </QuestionsList>
            </QuestionsContainer>
         </Container>
      )
   }
}
