import React, { Component } from 'react'
import {
   FaRegFileAlt,
   FaHighlighter,
   FaThumbtack,
   FaFilter,
   FaSearch,
   FaTrashAlt,
} from 'react-icons/fa'
import Select from 'react-select'
import { toast } from 'react-toastify'

import api from '../../services/api'
import history from '../../services/history'

import {
   Container,
   Title,
   FormContainer,
   TextHeader,
   Form,
   AreasContainer,
   ButtonsContainer,
   SubmitButton,
   ClearButton,
} from './styles'

export default class Pratice extends Component {
   state = {
      exam_selected: [],
      exams: [
         { value: 'poscomp', label: 'POSCOMP' },
         { value: 'enade', label: 'ENADE' },
      ],
      areas_selected: [],
      exam_areas: [],
      poscomp_areas: [
         { value: '1', label: 'Matemática' },
         { value: '2', label: 'Fundamentos de Computação' },
         { value: '3', label: 'Tecnologia da Computação' },
      ],
      enade_areas: [],
      questions: [],
   }

   handleChangeExam = event => {
      const { exam_selected, poscomp_areas, enade_areas } = this.state

      if (exam_selected !== event) {
         var exam_areas =
            event.value === 'poscomp' ? poscomp_areas : enade_areas
         exam_areas.forEach(area => {
            area.is_selected = false
            area.amount = 0
         })

         this.setState({
            exam_selected: event,
            areas_selected: [],
            exam_areas,
         })
      }
   }

   handleSelectArea = index => {
      var { exam_areas } = this.state

      exam_areas[index].is_selected = !exam_areas[index].is_selected
      exam_areas[index].amount = exam_areas[index].is_selected ? 1 : 0

      this.setState({ exam_areas })
   }

   handleChangeAreaAmount = (event, index) => {
      var { exam_areas } = this.state

      exam_areas[index].amount = event.target.value

      this.setState({ exam_areas })
   }

   verifySelectedAreas = areas => {
      const selected_areas = areas.filter(area => area.is_selected)

      if (selected_areas.length === 0) {
         return false
      }

      return selected_areas.filter(area => area.amount > 0).length !== 0
   }

   handleSubmitForm = async event => {
      event.preventDefault()

      const { exam_selected, exam_areas } = this.state

      if (Object.keys(exam_selected).length === 0) {
         toast.error('Selecione um concurso')
         return
      }

      if (!this.verifySelectedAreas(exam_areas)) {
         toast.error('Selecione ao menos uma questão')
         return
      }

      const areas_aux = exam_areas.filter(area => area.amount > 0)

      const response = await api.post('/pratice', {
         exam: exam_selected.value,
         areas: areas_aux.map(area => ({
            id: area.value,
            amount: area.amount,
         })),
      })

      console.log(`Redirect to /simulados/${response.data.id}`)

      history.push(`/simulados/${response.data.id}`)
   }

   handleClearForm = event => {
      event.preventDefault()

      this.setState({
         exam_selected: {},
         exam_areas: [],
      })
   }

   render() {
      const { exam_selected, exams, exam_areas } = this.state

      return (
         <Container>
            <Title>
               <FaRegFileAlt size={24} />
               <span>Simulados</span>
            </Title>
            <FormContainer>
               <TextHeader>
                  <FaFilter size={18} />
                  <span>Gerador</span>
               </TextHeader>
               <Form>
                  <p>Concurso</p>
                  <Select
                     isSearchable={false}
                     isClearable
                     value={exam_selected}
                     options={exams}
                     onChange={this.handleChangeExam}
                  />
                  <p>Áreas</p>
                  <AreasContainer>
                     {exam_areas.map((area, index) => (
                        <div key={area.value}>
                           <label>
                              <input
                                 type="checkbox"
                                 checked={area.is_selected}
                                 onChange={() => this.handleSelectArea(index)}
                              />
                              <span>{area.label}</span>
                           </label>
                           <div>
                              <span>Quantidade:</span>
                              <input
                                 type="number"
                                 min={1}
                                 max={20}
                                 disabled={!area.is_selected}
                                 value={area.amount}
                                 onChange={event =>
                                    this.handleChangeAreaAmount(event, index)
                                 }
                              />
                           </div>
                        </div>
                     ))}
                  </AreasContainer>
                  <ButtonsContainer>
                     <SubmitButton onClick={this.handleSubmitForm}>
                        <FaSearch size={16} />
                        <span>Gerar</span>
                     </SubmitButton>
                     <ClearButton onClick={this.handleClearForm}>
                        <FaTrashAlt size={16} />
                        <span>Limpar</span>
                     </ClearButton>
                  </ButtonsContainer>
               </Form>
            </FormContainer>
         </Container>
      )
   }
}
