import React, { Component } from 'react'
import {
   FaRegFileAlt,
   FaPencilAlt,
   FaUserTimes,
   FaUserCheck,
} from 'react-icons/fa'
import { MdRefresh } from 'react-icons/md'

import history from '../../services/history'

import {
   Container,
   Title,
   TextHeader,
   ResultContainer,
   ResultHeader,
   ResultList,
   StatisticsContainer,
   ButtonsContainer,
} from './styles'

export default class PraticeResult extends Component {
   state = {
      id: 0,
      results: [],
      statistics: {},
   }

   async componentDidMount() {
      const { id } = this.props.match.params

      const results = JSON.parse(
         await localStorage.getItem('@simulado/resultado')
      )

      console.log(results)

      if (results === null || results.id !== id) {
         history.push({
            pathname: `/simulados/${id}`,
         })
      }

      const { results: questions_results } = results

      const total_questions = questions_results.length
      const total_corrects = questions_results.filter(
         res => res.user_answer === res.answer
      ).length

      const statistics = {
         // percent: Math.floor((total_corrects * 100) / total_questions),
         percent: ((total_corrects / total_questions) * 100).toFixed(2) + '%',
         corrects: total_corrects,
         wrongs: total_questions - total_corrects,
         total: total_questions,
      }

      this.setState({ id, results: questions_results, statistics })
   }

   handleNewPratice = () => {
      history.push(`/simulados`)
   }

   handleRepeatPratice = () => {
      const { id } = this.state

      history.push(`/simulados/${id}`)
   }

   render() {
      const { id, results, statistics } = this.state

      return (
         <Container>
            <Title>
               <FaRegFileAlt size={24} />
               <span>Simulados</span>
            </Title>
            <TextHeader>
               <div>
                  <FaPencilAlt size={18} />
                  <span>Resultados</span>
               </div>
               <span>Código: #{id}</span>
            </TextHeader>
            <ResultContainer>
               <ResultHeader>
                  <span>Questão</span>
                  <span>Gabarito</span>
                  <span>Resposta</span>
                  <span>Acertou?</span>
               </ResultHeader>
               <ResultList>
                  {results.map(question => (
                     <li key={question.number}>
                        <span>{question.number}</span>
                        <span>{question.answer}</span>
                        <span>{question.user_answer}</span>
                        <span>
                           {question.answer === question.user_answer ? (
                              <FaUserCheck color="#0F0" />
                           ) : (
                              <FaUserTimes color="#F00" />
                           )}
                        </span>
                     </li>
                  ))}
               </ResultList>
               <StatisticsContainer>
                  <div>
                     <strong>{statistics.percent}</strong>
                     <span>Acertos</span>
                  </div>
                  <div>
                     <strong>{statistics.total}</strong>
                     <span>Questões</span>
                  </div>
                  <div>
                     <strong>{statistics.corrects}</strong>
                     <span>Corretas</span>
                  </div>
                  <div>
                     <strong>{statistics.wrongs}</strong>
                     <span>Erradas</span>
                  </div>
               </StatisticsContainer>
               <ButtonsContainer>
                  <button onClick={this.handleNewPratice}>
                     <FaRegFileAlt size={14} />
                     <span>Novo Simulado</span>
                  </button>
                  <button onClick={this.handleRepeatPratice}>
                     <MdRefresh size={16} />
                     <span>Repetir Simulado</span>
                  </button>
               </ButtonsContainer>
            </ResultContainer>
         </Container>
      )
   }
}
