import React, { Component } from 'react'
import { FaPen, FaHighlighter } from 'react-icons/fa'

import api from '../../services/api'

import Loading from '../../components/Loading'
import {
   Container,
   Title,
   QuestionsContainer,
   QuestionContent,
   TextHeader,
   QuestionStatement,
   AlternativesList,
   QuestionsInfoContainer,
} from './modal_styles'

export default class ModalQuestion extends Component {
   state = {
      loading: true,
      question: {},
   }

   componentDidMount() {
      this.setState({ question: this.props.question, loading: false })
   }

   render() {
      const { loading, question } = this.state

      if (loading) {
         return <Loading />
      }

      return (
         <Container>
            <Title>
               <FaPen size={24} />
               <span>Detalhes da Questão</span>
            </Title>
            <QuestionsContainer>
               <TextHeader>
                  <FaHighlighter size={18} />
                  <span>
                     ({question.exam}/{question.year}) / Questão{' '}
                     {question.number}
                  </span>
               </TextHeader>
               <QuestionContent>
                  <QuestionStatement
                     dangerouslySetInnerHTML={{
                        __html: question.statement,
                     }}
                  />
                  <AlternativesList>
                     {question.alternatives.map(alternative => (
                        <li key={alternative.letter}>
                           <strong>{alternative.letter})</strong>
                           <span
                              dangerouslySetInnerHTML={{
                                 __html: alternative.content,
                              }}
                           />
                        </li>
                     ))}
                  </AlternativesList>
                  <QuestionsInfoContainer>
                     <div>
                        <span>Gabarito da questão</span>
                        <p>Opção {question.answer}</p>
                     </div>
                     <div>
                        <span>Área da questão</span>
                        <p>{question.area}</p>
                     </div>
                  </QuestionsInfoContainer>
               </QuestionContent>
            </QuestionsContainer>
         </Container>
      )
   }
}
