import React, { Component } from 'react'
import Select from 'react-select'
import { toast } from 'react-toastify'
import {
   FaSearch,
   FaPlus,
   FaPencilAlt,
   FaUserCog,
   FaWindowClose,
} from 'react-icons/fa'

import Modal from 'react-modal'
import ModalQuestion from './modal'

import api from '../../services/api'

import {
   Container,
   QuestionModal,
   Title,
   TextHeader,
   ButtonsContainer,
   AddQuestionButton,
   AdminContainer,
   LabelContainer,
   NumberInput,
   InputStatementList,
   InputStatementItem,
   StatementInput,
   AlternativesList,
   AlternativeInput,
} from './styles'

const exam_options = [
   { value: 'poscomp', label: 'POSCOMP', index: 0 },
   { value: 'enade', label: 'ENADE', index: 1 },
]

const year_options = [
   { value: '2004', label: '2004' },
   { value: '2005', label: '2005' },
   { value: '2006', label: '2006' },
   { value: '2007', label: '2007' },
   { value: '2008', label: '2008' },
   { value: '2009', label: '2009' },
   { value: '2010', label: '2010' },
   { value: '2011', label: '2011' },
   { value: '2012', label: '2012' },
   { value: '2013', label: '2013' },
   { value: '2014', label: '2014' },
   { value: '2015', label: '2015' },
   { value: '2016', label: '2016' },
   { value: '2017', label: '2017' },
   { value: '2018', label: '2018' },
   { value: '2019', label: '2019' },
]

const answer_options = [
   { value: 'A', label: 'A' },
   { value: 'B', label: 'B' },
   { value: 'C', label: 'C' },
   { value: 'D', label: 'D' },
   { value: 'E', label: 'E' },
]

const areas_options = [
   [
      { value: '1', label: 'Matemática' },
      { value: '2', label: 'Fundamentos de Computação' },
      { value: '3', label: 'Tecnologia da Computação' },
   ],
]

const type_options = [
   { value: 1, label: 'Normal' },
   { value: 2, label: 'Center' },
   { value: 3, label: 'Image' },
   { value: 4, label: 'Code' },
]

Modal.setAppElement('#root')

export default class Admin extends Component {
   state = {
      question: {},
      show_modal: false,
      exam_input: {},
      question_input: 1,
      year_input: {},
      answer_input: {},
      area_input: {},
      statement_input: [{ id: 0, type: 1, content: '' }],
      alternatives_input: [
         { letter: 'A', content: '' },
         { letter: 'B', content: '' },
         { letter: 'C', content: '' },
         { letter: 'D', content: '' },
         { letter: 'E', content: '' },
      ],
   }

   componentDidMount() {
      const {
         exam_input,
         question_input,
         year_input,
         answer_input,
         area_input,
         statement_input,
         alternatives_input,
      } = this.state

      this.setState({
         question: {
            exam: exam_input.label,
            number: question_input,
            year: year_input.value,
            answer: answer_input.value,
            area: area_input.label,
            statement: statement_input,
            alternatives: alternatives_input,
         },
      })
   }

   handleChangeExamInput = option => {
      this.setState({
         exam_input: option,
         area_input: {},
      })
   }

   handleChangeQuestionNumber = event => {
      this.setState({ question_input: event.target.value })
   }

   handleChangeYearInput = option => {
      this.setState({ year_input: option })
   }

   handleChangeAnswerInput = option => {
      this.setState({ answer_input: option })
   }

   handleChangeAreaInput = option => {
      this.setState({ area_input: option })
   }

   handleAddStatement = event => {
      const { statement_input } = this.state

      this.setState({
         statement_input: [
            ...statement_input,
            { id: statement_input.length, type: 1, content: '' },
         ],
      })
   }

   handleTypeInputChange = (option, id) => {
      const { statement_input } = this.state

      const index = statement_input.findIndex(s => s.id === id)
      statement_input[index].type = option.value

      this.setState({ statement_input })
   }

   handleContentInputChange = (event, id) => {
      const { statement_input } = this.state

      const index = statement_input.findIndex(s => s.id === id)

      statement_input[index].content = event.target.value

      this.setState({ statement_input })
   }

   handleAlternativesInputChange = (event, letter) => {
      const { alternatives_input } = this.state

      const index = alternatives_input.findIndex(a => a.letter === letter)
      alternatives_input[index].content = event.target.value

      this.setState({ alternatives_input })
   }

   isStatementEmpty = statement => {
      return statement.filter(s => s.content === '').length > 0
   }

   isAlternativesEmpty = alternatives => {
      return alternatives.filter(a => a.content === '').length > 0
   }

   handleAddQuestion = async () => {
      const {
         exam_input,
         question_input,
         year_input,
         answer_input,
         area_input,
         statement_input,
         alternatives_input,
      } = this.state

      if (Object.keys(exam_input).length === 0) {
         toast.error('Selecione uma PROVA')
         return
      }

      if (Object.keys(year_input).length === 0) {
         toast.error('Preencha o ANO da questão')
         return
      }

      if (Object.keys(answer_input).length === 0) {
         toast.error('Preencha o GABARITO da questão')
         return
      }

      if (Object.keys(area_input).length === 0) {
         toast.error('Preencha a ÁREA da questão')
         return
      }

      if (this.isStatementEmpty(statement_input)) {
         toast.error('Preencha todos os ENUNCIADOS')
         return
      }

      if (this.isAlternativesEmpty(alternatives_input)) {
         toast.error('Preencha todas as ALTERNATIVAS')
         return
      }

      const response = await api.post('/questions', {
         exam: exam_input.value,
         alternatives: alternatives_input,
         answer: answer_input.value,
         area: area_input.value,
         justification: 'Justificativa',
         number: question_input,
         statement: statement_input,
         year: year_input.value,
      })

      console.log(response)

      toast.success('Questão adicionada com sucesso')
      this.setState({
         exam_input: {},
         question_input: 1,
         year_input: {},
         answer_input: {},
         area_input: {},
         statement_input: [{ id: 0, type: 1, content: '' }],
         alternatives_input: [
            { letter: 'A', content: '' },
            { letter: 'B', content: '' },
            { letter: 'C', content: '' },
            { letter: 'D', content: '' },
            { letter: 'E', content: '' },
         ],
      })
   }

   handleOpenModal = () => {
      const {
         exam_input,
         question_input,
         year_input,
         answer_input,
         area_input,
         statement_input,
         alternatives_input,
      } = this.state

      var statement_string = ''
      for (const s of statement_input) {
         var aux = ''
         switch (s.type) {
            // Normal
            case 1:
               aux = `<p>${s.content}</p>`
               break
            // Center
            case 2:
               aux = `<center>${s.content}</center>`
               break
            // Image
            case 3:
               aux = `<center><img style="width: 35%" src="${s.content}" /></center>`
               break
            // Code
            case 4:
               aux = `<p><code><pre>${s.content}</pre></code></p>`
               break
         }
         statement_string += aux
      }

      this.setState({
         question: {
            exam: exam_input.label,
            number: question_input,
            year: year_input.value,
            answer: answer_input.value,
            area: area_input.label,
            statement: statement_string,
            alternatives: alternatives_input,
         },
         show_modal: true,
      })
   }

   handleCloseModal = () => {
      this.setState({ show_modal: false })
   }

   render() {
      const {
         question,
         show_modal,
         exam_input,
         question_input,
         year_input,
         answer_input,
         area_input,
         statement_input,
         alternatives_input,
      } = this.state

      return (
         <Container>
            <QuestionModal
               isOpen={show_modal}
               contentLabel="Minimal Modal Example"
            >
               <button onClick={this.handleCloseModal}>
                  <FaWindowClose size={36} />
               </button>
               <strong>Preview</strong>
               <ModalQuestion question={question} />
            </QuestionModal>
            <Title>
               <FaUserCog size={24} />
               <span>Admin</span>
            </Title>
            <TextHeader>
               <div>
                  <FaPencilAlt size={18} />
                  <span>Adicionar Questão</span>
               </div>
               <ButtonsContainer>
                  <AddQuestionButton onClick={this.handleAddQuestion}>
                     <FaPlus />
                     Adicionar
                  </AddQuestionButton>
                  <AddQuestionButton onClick={this.handleOpenModal}>
                     <FaSearch />
                     Preview
                  </AddQuestionButton>
               </ButtonsContainer>
            </TextHeader>
            <AdminContainer>
               <header>
                  <strong>Descrição da Questão</strong>
               </header>
               <div>
                  <LabelContainer>
                     <span>Prova</span>
                     <Select
                        isSearchable={false}
                        isClearable={false}
                        value={exam_input}
                        options={exam_options}
                        onChange={this.handleChangeExamInput}
                     />
                  </LabelContainer>
               </div>
               <div>
                  <LabelContainer>
                     <span>Questão</span>
                     <NumberInput
                        value={question_input}
                        onChange={this.handleChangeQuestionNumber}
                     />
                  </LabelContainer>
                  <LabelContainer>
                     <span>Ano</span>
                     <Select
                        isSearchable={false}
                        isClearable
                        value={year_input}
                        options={year_options}
                        onChange={this.handleChangeYearInput}
                     />
                  </LabelContainer>
               </div>
               <div>
                  <LabelContainer>
                     <span>Gabarito</span>
                     <Select
                        isSearchable={false}
                        isClearable
                        value={answer_input}
                        options={answer_options}
                        onChange={this.handleChangeAnswerInput}
                     />
                  </LabelContainer>
                  <LabelContainer>
                     <span>Area</span>
                     <Select
                        isSearchable={false}
                        isClearable
                        value={area_input}
                        options={areas_options[exam_input.index]}
                        onChange={this.handleChangeAreaInput}
                     />
                  </LabelContainer>
               </div>
               <header>
                  <strong>Enunciado da Questão</strong>
                  <button onClick={this.handleAddStatement}>
                     <FaPlus />
                     Enunciado
                  </button>
               </header>
               <div>
                  <InputStatementList>
                     {statement_input.map(s => (
                        <InputStatementItem key={s.id}>
                           <Select
                              isSearchable={false}
                              isClearable
                              options={type_options}
                              defaultValue={type_options[0]}
                              onChange={option =>
                                 this.handleTypeInputChange(option, s.id)
                              }
                           />
                           <StatementInput
                              value={s.content}
                              onChange={event =>
                                 this.handleContentInputChange(event, s.id)
                              }
                           />
                        </InputStatementItem>
                     ))}
                  </InputStatementList>
               </div>
               <header>
                  <strong>Alternativas da Questão</strong>
               </header>
               <div>
                  <AlternativesList>
                     {alternatives_input.map((a, id) => (
                        <div key={a.letter}>
                           <strong>{a.letter}) </strong>
                           <AlternativeInput
                              value={a.content}
                              onChange={event =>
                                 this.handleAlternativesInputChange(
                                    event,
                                    a.letter
                                 )
                              }
                           />
                        </div>
                     ))}
                  </AlternativesList>
               </div>
            </AdminContainer>
         </Container>
      )
   }
}
