import React, { Component } from 'react'

import {
   FaRegFileAlt,
   FaFilter,
   FaRegFilePdf,
   FaHourglassEnd,
   FaArrowLeft,
   FaArrowRight,
} from 'react-icons/fa'
import { toast } from 'react-toastify'

import history from '../../services/history'
import api from '../../services/api'

import {
   Container,
   Title,
   TextHeader,
   PraticeContainer,
   HeaderContainer,
   AnswerList,
   OptionButtonsContainer,
   EndPraticeButton,
   DownloadPDFButton,
   ControlsContainer,
   Control,
   QuestionHeader,
   QuestionStatement,
   AlternativesList,
   AnswerContainer,
   AnswerContent,
   AnswerButton,
} from './styles'

export default class Pratice extends Component {
   state = {
      id: 0,
      questions: [],
      id_current_question: 0,
   }

   async componentDidMount() {
      const { id } = this.props.match.params

      const { data } = await api.get(`/pratice/${id}`)

      const questions = data.questions.map((question, index) => ({
         ...question,
         pratice_number: ('0' + (index + 1)).slice(-2),
         user_answer: '-',
         alternatives: question.alternatives.reverse(),
      }))

      this.setState({
         id: data.id,
         questions,
      })
   }

   handleEndPratice = async questions => {
      const { id } = this.state

      await localStorage.setItem(
         '@simulado/resultado',
         JSON.stringify({
            id,
            results: questions.map(q => ({
               number: q.pratice_number,
               user_answer: q.user_answer,
               answer: q.answer,
            })),
         })
      )

      history.push({
         pathname: `/simulados/${id}/resultado`,
      })
   }

   handleDownloadPDF = () => {
      const { id } = this.state

      // history.push(`/pdf/${id}`)
   }

   handleChangeQuestion = value => {
      const { questions, id_current_question } = this.state
      const new_question = id_current_question + value

      if (new_question >= 0 && new_question < questions.length) {
         this.setState({ id_current_question: new_question })
      }
   }

   handleSelectAnswer = (id, letter) => {
      const { questions } = this.state

      questions[Number(id) - 1].user_answer = letter

      this.setState({ questions })
   }

   render() {
      const { id, questions, id_current_question } = this.state

      const current_question = questions[id_current_question]

      if (questions.length === 0) {
         return <h1>Loading</h1>
      }

      return (
         <Container>
            <Title>
               <FaRegFileAlt size={24} />
               <span>Simulados</span>
            </Title>
            <TextHeader>
               <div>
                  <FaFilter size={18} />
                  <span>Resolver Simulado</span>
               </div>
               <span>Código: #{id}</span>
            </TextHeader>
            <PraticeContainer>
               <HeaderContainer>
                  <AnswerList>
                     {questions.map((question, index) => (
                        <li key={index}>
                           <span>{question.pratice_number}</span>
                           <strong>{question.user_answer}</strong>
                        </li>
                     ))}
                  </AnswerList>
                  <OptionButtonsContainer>
                     <EndPraticeButton
                        onClick={() => this.handleEndPratice(questions)}
                     >
                        <FaHourglassEnd />
                        <span>Finalizar</span>
                     </EndPraticeButton>
                     <DownloadPDFButton onClick={this.handleDownloadPDF}>
                        <FaRegFilePdf />
                        <span>Download PDF</span>
                     </DownloadPDFButton>
                  </OptionButtonsContainer>
               </HeaderContainer>
               <ControlsContainer>
                  <Control onClick={() => this.handleChangeQuestion(-1)}>
                     <FaArrowLeft size={14} />
                     <span>Anterior</span>
                  </Control>
                  {/* <p>Questão {current_question.pratice_number}</p> */}
                  <Control onClick={() => this.handleChangeQuestion(1)}>
                     <span>Próxima</span>
                     <FaArrowRight size={14} />
                  </Control>
               </ControlsContainer>
               <QuestionHeader>
                  <strong>
                     <span>{current_question.pratice_number}</span>
                     (POSCOMP/{current_question.year} - Questão{' '}
                     {current_question.number})
                  </strong>
                  <span>#{current_question.id}</span>
               </QuestionHeader>
               <QuestionStatement
                  dangerouslySetInnerHTML={{
                     __html: current_question.statement,
                  }}
               />
               <AlternativesList>
                  {current_question.alternatives.map(alternative => (
                     <li key={alternative.letter}>
                        <strong>{alternative.letter})</strong>
                        <span
                           dangerouslySetInnerHTML={{
                              __html: alternative.content,
                           }}
                        />
                     </li>
                  ))}
               </AlternativesList>
               <AnswerContainer>
                  <p>Escolha sua resposta</p>
                  <AnswerContent>
                     {current_question.alternatives.map(alternative => (
                        <AnswerButton
                           selected={
                              alternative.letter ===
                              current_question.user_answer
                           }
                           key={alternative.letter}
                           onClick={() =>
                              this.handleSelectAnswer(
                                 current_question.pratice_number,
                                 alternative.letter
                              )
                           }
                        >
                           {alternative.letter}
                        </AnswerButton>
                     ))}
                  </AnswerContent>
               </AnswerContainer>
            </PraticeContainer>
         </Container>
      )
   }
}
